@import url(https://fonts.googleapis.com/css2?family=Nova+Round&display=swap);
*{
  margin: 0;
  padding: 0;
  font-family: 'Nova+Round', sans-serif;
  color: black;
}

.app{
  position: relative;
}

.next-clock-button-container{
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 1;
}

.next-clock-button{
  color: black;
  margin: auto;
  margin-top: .3rem;
  display: block;
  cursor: pointer;
  padding: .2rem;
  transition: all .2s ease-in-out;
}
.clock-container{
    position: relative;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: black;
    display: -webkit-flex;
    display: flex;
}

.clock{
    position: relative;
    height: 50vmin;
    width: 50vmin;
    background-color: white;
    border-radius: 50%;
    margin: auto auto;
    border: 1px solid black;
    box-shadow: 0 0 3px 4px yellow,
                0 0 6px 8px yellow,
                0 0 9px 12px yellow,
                0 0 12px 16px yellow,
                0 0 15px 20px yellow;
    -webkit-animation: animate-color 7s linear 0s infinite;
            animation: animate-color 7s linear 0s infinite;
}

.clock::after{
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    height: 15px;
    width: 15px;
    border-radius: 50%;
    background-color: black;
}

.number{
    position: absolute;
    text-align: center;
    height: 100%;
    width: 100%;
    font-size: 2rem;
    -webkit-transform: rotate(var(--rotateBy));
            transform: rotate(var(--rotateBy));
}

.hand{
    position: absolute;
    bottom: 50%;
    left: 50%;
    border-radius: 10px;
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
}

.hour-hand{
    width: 8px;
    height: 20%;
    background-color: black;
    -webkit-transform: translateX(-50%) rotate(var(--rotateBy));
            transform: translateX(-50%) rotate(var(--rotateBy));
}

.minute-hand{
    width: 5px;
    height: 35%;
    background-color: black;
    -webkit-transform: translateX(-50%) rotate(var(--rotateBy));
            transform: translateX(-50%) rotate(var(--rotateBy));
}
.second-hand{
    width: 2px;
    height: 42%;
    background-color: red;
    -webkit-transform: translateX(-50%) rotate(var(--rotateBy));
            transform: translateX(-50%) rotate(var(--rotateBy));
}

.digital-time{
    position: absolute;
    bottom: 5%;
    left: 50%;
    color: white;
    font-size: 3rem;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    padding: .5rem;
    box-shadow: 0 0 3px 3px yellow,
                0 0 5px 4px yellow,
                0 0 7px 5px yellow,
                0 0 9px 6px yellow,
                0 0 11px 7px yellow;
                -webkit-animation: animate-color 5s linear 0s infinite;
                        animation: animate-color 5s linear 0s infinite;
}

@-webkit-keyframes animate-color {
    0%{ -webkit-filter: hue-rotate(0deg); filter: hue-rotate(0deg);}
    100%{ -webkit-filter: hue-rotate(360deg); filter: hue-rotate(360deg);}
}

@keyframes animate-color {
    0%{ -webkit-filter: hue-rotate(0deg); filter: hue-rotate(0deg);}
    100%{ -webkit-filter: hue-rotate(360deg); filter: hue-rotate(360deg);}
}


@media (max-width : 1024px) {
    .clock{
        height: 70vmin;
        width: 70vmin;
    }

    .digital-time{
        bottom: 10%;
        left: 50%;
        font-size: 1.5rem;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
        padding: .2rem;

    }
}
.digital-clock-container{
    position: relative;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: black;
    display: -webkit-flex;
    display: flex;
}

.digital-clock-time{
    position: absolute;
    bottom: 40%;
    left: 50%;
    color: white;
    font-size: 3rem;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    padding: .5rem;
    box-shadow: 0 0 3px 3px yellow,
                0 0 5px 4px yellow,
                0 0 7px 5px yellow,
                0 0 9px 6px yellow,
                0 0 11px 7px yellow;
    -webkit-animation: animate-color 5s linear 0s infinite;
            animation: animate-color 5s linear 0s infinite;
}

@-webkit-keyframes animate-color {
    0%{ -webkit-filter: hue-rotate(0deg); filter: hue-rotate(0deg);}
    100%{ -webkit-filter: hue-rotate(360deg); filter: hue-rotate(360deg);}
}

@keyframes animate-color {
    0%{ -webkit-filter: hue-rotate(0deg); filter: hue-rotate(0deg);}
    100%{ -webkit-filter: hue-rotate(360deg); filter: hue-rotate(360deg);}
}


@media (max-width : 860px) {
    .clock{
        height: 70vmin;
        width: 70vmin;
    }

    .digital-clock-time{
        bottom: 40%;
        left: 50%;
        font-size: 1.5rem;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
        padding: .2rem;

    }
}
.digital-dot-clock-container{
    position: relative;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgb(23, 25, 36);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
}

.digital-dot-clock{
    position: relative;
    height: 250px;
    width: 250px;
}

.digital-dot-clock-hand-container{
    position: absolute;
    height: 100%;
    width: 100%;
}

.digital-dot-clock-hand-svg{
    position: relative;
    height: 100%;
    width: 100%;
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
    opacity: .5;
}
.digital-dot-clock-second-hand{
    cx: 125;
    cy: 125;
    r: 120;
    fill: transparent;
    stroke: var(--clr);
    stroke-width: 4;
    stroke-dasharray: 754;
    stroke-dashoffset: var(--secondDashOffset);
}
.digital-dot-clock-minute-hand{
    cx: 125;
    cy: 125;
    r: 100;
    fill: transparent;
    stroke: var(--clr);
    stroke-width: 4;
    stroke-dasharray: 628;
    stroke-dashoffset: var(--minuteDashOffset);
}
.digital-dot-clock-hour-hand{
    cx: 125;
    cy: 125;
    r: 80;
    fill: transparent;
    stroke: var(--clr);
    stroke-width: 4;
    stroke-dasharray: 503;
    stroke-dashoffset: var(--hourDashOffset);
}

.digital-dot-clock-hand-dot{
    position: absolute;
    height: 100%;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-transform: rotate(var(--dotRotationDegree)) translateY(var(--translateY));
            transform: rotate(var(--dotRotationDegree)) translateY(var(--translateY));
}

.digital-dot-clock-hand-dot::after{
    content: "";
    position: absolute;
    height: 10px;
    width: 10px;
    border-radius: 50%;
    background-color: var(--clr);
    box-shadow: 0px 0px 5px 2px var(--clr),
    0px 0px 15px 3px var(--clr),
    0px 0px 25px 5px var(--clr);
}

/* Digital clock */
.digital-dot-clock-digital-clock{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.digital-dot-clock-digital-clock-digit{
    font-size: 1.3rem;
    font-weight: 600;
    color: var(--clr);
    text-shadow: 0px 0px 10px var(--clr),
                0px 0px 20px var(--clr),
                0px 0px 30px var(--clr);
}

.digital-dot-clock-digital-clock-ampm-digit{
    font-size: .5rem;
    font-weight: 600;
    color: white;
    text-shadow: 0px 0px 10px white,
                0px 0px 20px white,
                0px 0px 30px white;
}


.dot-clock-container{
    position: relative;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgb(23, 25, 36);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
}

/* Square Box for each hand */
.dot-clock-hand{
    position: relative;
    height: 100px;
    width: 100px;
    /* border: 1px solid red; */
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

/* SVG  */
.dot-clock-circle-container-svg{
    position: absolute;
    height: 100%;
    width: 100%;
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
}

.dot-clock-circle-1{
    cx: 50px;
    cy: 50px;
    r: 45px;
    height: 50%;
    width: 50%;
    fill: transparent;
    stroke: black;
    stroke-width: 4;
}

.dot-clock-circle-2{
    cx: 50px;
    cy: 50px;
    r: 45px;
    height: 50%;
    width: 50%;
    fill: transparent;
    stroke: var(--clr);
    stroke-width: 4;
    stroke-dasharray: 284;
    stroke-dashoffset: var(--strokeDashOffset);
}

/* Dot */
.dot-clock-dot{
    position: absolute;
    -webkit-transform: rotate(var(--dotRotationDeg));
            transform: rotate(var(--dotRotationDeg));
    height: 100%;
    width: 100%;
    z-index: 10;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    /* border: 1px solid red; */
}

.dot-clock-dot::after{
    content: "";
    position: absolute;
    height: 10px;
    width: 10px;
    background-color: var(--clr);
    border-radius: 50%;
    box-shadow: 0px 0px 15px var(--clr),
                0px 0px 25px var(--clr),
                0px 0px 25px var(--clr),
                0px 0px 25px var(--clr);
}

/* Texts */
.dot-clock-hand-text-container{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.dot-clock-hand-identifier{
    color: white;
    text-transform: uppercase;
    font-size: .6rem;
}

.dot-clock-hand-text{
    font-size: 1.5rem;
    font-weight: 600;
    color: white;
}

.dot-clock-ampm-text{
    /* position: absolute; */
    color: white;
    font-size: 1.5rem;
    font-weight: 600;
}

